@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 80px;
    .navegacion {
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        @include m.tablet {
            width: 400px;
        }
       
        i {
            position: relative;
            font-size: 2.5rem;
            font-family: v.$texto;
            color: v.$blanco;
            border-radius: 50%;
            padding: 1rem 1.1rem;
            transition: all .2s ease-in-out;
            &:hover {
                color: black;
            }
        }
    }
}

.navegacion {

    .bi-instagram {

        &::after {
            content: 'Instagram';
            position: absolute;
            bottom: -12px;
            left: -12px;
            font-size: 1.8rem;
            overflow: hidden;
            display: none;
        }
        
        &:hover::after {
            animation: redes .3s ease-in-out;
            display: block;
            overflow: visible;
        }
    }
    .bi-github {
        &::after {
            content: 'Github';
            position: absolute;
            bottom: -12px;
            left: -3px;
            font-size: 1.8rem;
            overflow: hidden;
            display: none;
        }

        &:hover::after {
            animation: redes .3s ease-in-out;
            display: block;
            overflow: visible;
        }
    }
    .bi-linkedin {
        &::after {
            content: 'Linkedin';
            position: absolute;
            bottom: -12px;
            left: -8px;
            font-size: 1.8rem;
            overflow: hidden;
            display: none;
        }

        &:hover::after {
            animation: redes .3s ease-in-out;
            display: block;
            overflow: visible;
        }
    }
}